
import { Box, Button, Typography } from "@mui/material/";
import { useState, useEffect, useMemo } from 'react';
import { NamedUrl } from "../../midi/load";

interface FileUploaderProps {
  midiUrl: NamedUrl;
  audioUrl: NamedUrl;
  setMidiUrl: React.Dispatch<React.SetStateAction<NamedUrl>>;
  setAudioUrl: React.Dispatch<React.SetStateAction<NamedUrl>>;
}

const FileUploader: React.FC<FileUploaderProps> = ({
  midiUrl,
  audioUrl,
  setMidiUrl,
  setAudioUrl,
}) => {
  const [audioFile, setAudioFile] = useState<File | null>(null);
  const [midiFile, setMidiFile] = useState<File | null>(null);
  const [audioName, setAudioName] = useState<string>("");
  const [midiName, setMidiName] = useState<string>("");

  useEffect(() => {
    setAudioName("");
    setMidiName("");
  }, []);

  useEffect(() => {
    if (audioFile) setAudioName(audioFile.name);
  }, [audioFile]);

  useEffect(() => {
    if (midiFile) setMidiName(midiFile.name);
  }, [midiFile]);

  const handleAudioFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      setAudioFile(event.target.files[0]);
    }
  };

  const handleMidiFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.files && event.target.files.length > 0) {
      setMidiFile(event.target.files[0]);
    }
  };

  const handleEditClick = async () => {
    if (!audioFile) return;
    if (!midiFile) return;
    const upload = async (file: File): Promise<string> => {
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.onload = () => {
          const blobUrl = URL.createObjectURL(file);
          resolve(blobUrl);
        };
        reader.onerror = () => {
          reject(new Error('Failed to read file'));
        };
        reader.readAsArrayBuffer(file);
      });
    };

    const [audioDataUrl, midiDataUrl] = await Promise.all([
      upload(audioFile),
      upload(midiFile),
    ]);

    setAudioUrl({
      url:
        audioDataUrl,
      name: audioFile.name,
    });
    setMidiUrl({
      url: midiDataUrl,
      name: midiFile.name,
    });
  };

  const editEnabled = useMemo(() => {
    return !!audioFile && !!midiFile;
  }, [audioFile, midiFile]);

  return (
    <>
      <Typography variant="h4">Upload Files</Typography>
      <Typography variant="body1">Upload an audio file and a MIDI file to get started.</Typography>
      <Box component={"p"} sx={{ mt: 2 }}>
        <input
          type="file"
          accept="audio/*"
          onChange={handleAudioFileChange}
          style={{ display: 'none' }}
          id="audio-file-input"
        />
        <label htmlFor="audio-file-input">
          <Button variant="contained" component="span">
            Upload Audio
          </Button>
        </label>
        <input
          type="file"
          accept=".mid"
          onChange={handleMidiFileChange}
          style={{ display: 'none' }}
          id="midi-file-input"
        />
        <label htmlFor="midi-file-input">
          <Button variant="contained" component="span">
            Upload MIDI
          </Button>
        </label>
      </Box>
      {audioFile && (
        <Typography variant="body1">Audio file: {audioFile.name}</Typography>
      )}
      {midiFile && (
        <Typography variant="body1">MIDI file: {midiFile.name}</Typography>
      )}
      <Box>
        <Button
          variant="contained"
          onClick={handleEditClick}
          disabled={!editEnabled}
        >
          Upload
        </Button>
      </Box>
    </>
  );
}

export default FileUploader;