import { Shapes } from "../util/Shapes";
import { IMidiAnimation, Note, NoteFilterCriteria } from "../types";
import { BaseAnimationOptions, ICubesOptions } from "./types";
import { BaseMidiAnimation } from "./BaseMidiAnimation";

interface Options extends BaseAnimationOptions, ICubesOptions {
  count?: number;
  cubeHeight?: number;
  gap?: number;
  top?: boolean;
  right?: boolean;
  bottom?: boolean;
  left?: boolean;
  center?: boolean;
  mirroredX?: boolean;
  mirroredY?: boolean;
  minMidi?: number;
  maxMidi?: number;
}

/** 
 * These are the options for the Circles animation [[ICirclesOptions]]
 */
export class Cubes extends BaseMidiAnimation<Options> implements IMidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, "cubes", {
      count: "number",
      cubeHeight: "number",
      gap: "number",
      top: "boolean",
      right: "boolean",
      bottom: "boolean",
      left: "boolean",
      center: "boolean",
      mirroredX: "boolean",
      mirroredY: "boolean",
      minMidi: "number",
      maxMidi: "number",
    }, id);
  }

  public draw(notes: Note[], canvas: CanvasRenderingContext2D): void {
    const { height, width } = canvas.canvas;
    const shapes = new Shapes(canvas);

    const options = {
      gap: 2,
      minMidi: 0,
      maxMidi: 127,
      fillColor: this.options.lineColor,
      ...this.options,
    };

    const { x: originX, y: originY } = options.origin ?? { x: width / 2, y: height / 2 };

    const rectangle = (x: number, y: number, width: number, height: number, options: Options) => {
      shapes.rectangle(originX + x, originY + y, cubeWidth, options.cubeHeight, options);
    };

    if (this.options?.mirroredX) {
      // TODO
      // let n = 1;
      // for (let i = Math.ceil(audioData.data.length / 2); i < audioData.data.length; i++) {
      //   audioData.data[i] = audioData.data[Math.ceil(audioData.data.length / 2) - n];
      //   n++;
      // }
    }

    if (!options.count) options.count = options.maxMidi - options.minMidi;
    const cubeWidth = Math.floor((width - (options.gap * options.count)) / options.count);
    if (!options?.cubeHeight) options.cubeHeight = cubeWidth;

    if (options.top) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = (options.gap + cubeWidth) * i;
        let y = (options.cubeHeight + options.gap);
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }

    if (options.right) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = width - (options.gap + cubeWidth);
        let y = (options.cubeHeight + options.gap) * i;
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }
    if (options?.bottom || (!options?.top && !options?.right && !options?.left && !options?.center)) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = (options.gap + cubeWidth) * i;
        let y = height - (options.cubeHeight + options.gap);
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }

    if (options.left) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = (options.gap + cubeWidth);
        let y = (options.cubeHeight + options.gap) * i;
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }

    if (options.center) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = (options.gap + cubeWidth) * i;
        let y = (height / 2) - (options.cubeHeight + options.gap) * i;
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }

    if (options?.mirroredY) {
      for (let i = 0; i < options.count; i++) {
        const midi = options.minMidi + i;
        if (!notes.find(note => note.midi === midi)) continue;
        const x = (options.gap + cubeWidth) * i;
        let y = (height / 2) + (options.cubeHeight + options.gap) * i;
        rectangle(x, y, cubeWidth, options.cubeHeight, options);
      }
    }
  }
}