
import { Midi as ToneJSMidi } from "@tonejs/midi";


export interface NamedUrl {
  name: string;
  url: string;
}
export interface FileBundle {
  midi: NamedUrl;
  audio: NamedUrl;
  midizUrl: string;
}

export const loadFiles = async (f: FileBundle, opts?: { verbose?: boolean }) => {
  const { midi: { url } } = f;

  const { verbose = false } = opts || {};

  if (verbose) console.log("[loadFiles]", "loading", url);

  const midiBuffer = await (await fetch(url)).arrayBuffer();
  const midi = new ToneJSMidi(midiBuffer);
  const midiJSON = midi.toJSON();

  if (verbose) console.log("[loadFiles]", midiJSON);

  const tracks = midi.tracks.map((track, i) => {
    const instr = track.instrument.toJSON();
    const { family, name } = instr;
    const instrument = { family, name };
    const notes = track.notes.map(note => {
      const { midi, time, duration, velocity } = note;
      return { midi, time, duration, velocity };
    });
    return {
      index: i,
      name: track.name,
      minTimeSecs: track.notes[0].time,
      maxTimeSecs: track.notes[track.notes.length - 1].time + track.notes[track.notes.length - 1].duration,
      duration: track.duration,
      durationTicks: track.durationTicks,
      instrument,
      notes,
    };
  });

  if (verbose) console.log("[loadFiles]", "tracks", tracks);

  return tracks;
};