
import "../../sass/index.scss";
import { useWavejs } from '../../midi/useWavejs';
import { useEffect } from "react";
import { registerDemoAnimations } from "../../midi/demo";

export function Home() {
  const data = useWavejs();

  const { wave, registry } = data;

  useEffect(() => {
    if (!wave) return;
    if (!registry) return;

    const init = async () => {
      const source = await wave.addPlugin(registry, { eagerInit: true });

      registerDemoAnimations(source, registry);
    }
    init();
  }, [registry, wave]);

  return (<main className={"p-home"}>
    <section className={"__display"}>
      <div className={"__canvas"}>
        <canvas ref={data.canvasElm}></canvas>
      </div>
    </section>
    <audio controls
      src={`${process.env.PUBLIC_URL}/assets/nogo.mp3`}
      ref={data.audioElm}
      style={{
        width: '98%',
        margin: "auto",
      }}>
    </audio>
  </main>)
}


