import { Wave } from "@foobar404/wave";
import { useRef, useState, useEffect } from "react";
import { registerDemoAnimations } from "./demo";
import { MidiAnimationRegistry } from "./registry";

export function useWavejs(opts?: {
  registry?: MidiAnimationRegistry,
}) {
  const { registry: initialRegistry } = opts || {};

  const canvasElm = useRef<HTMLCanvasElement | null>(null);
  const audioElm = useRef<HTMLAudioElement | null>(null);
  const [wave, setWave] = useState<Wave | null>(null);
  const [registry, setRegistry] = useState<MidiAnimationRegistry | null>(initialRegistry || new MidiAnimationRegistry({ verbose: true }));

  useEffect(() => {
    if (!canvasElm.current) return;
    if (!audioElm.current) return;
    if (wave) return;

    console.log("audioElm.current", audioElm.current);


    const init = async () => {
      console.log("init wavejs");
      const newWave = new Wave(audioElm.current, canvasElm.current);

      setRegistry(registry);
      setWave(newWave);

      canvasElm.current.style.width = '100%';
      canvasElm.current.style.height = '100%';
      canvasElm.current.width = canvasElm.current.offsetWidth;
      canvasElm.current.height = canvasElm.current.offsetHeight;
    };
    init();
  }, [audioElm.current, canvasElm.current]);

  useEffect(() => {
    if (!wave) return;
    // TODO
  }, [wave]);

  return {
    audioElm,
    canvasElm,
    wave,
    registry,
  };
}
