import React, { useCallback, useMemo, useState } from "react";

import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle } from "@mui/material";
import { IMidiAnimationJSON } from '../../midi/animations/util';
import { BaseAnimationOptions, OptionsPrototype } from "../../midi/animations/types";
import SimpleAnimationEditor from "./SimpleAnimationEditor";

interface AnimationAndOptions {
  animation: IMidiAnimationJSON;
  optionsPrototype: OptionsPrototype;
}

interface EditDialogProps {
  isOpen: boolean;
  onClose: () => void;
  animation: AnimationAndOptions;
}

const EditDialog = (props: EditDialogProps) => {
  const { isOpen, onClose, animation } = props;

  const [changed, setChanged] = useState(false);
  const initialOptions = useMemo(() => animation?.animation?.options || {}, [animation]);
  const [optionsToEdit, setOptionsToEdit] = useState<BaseAnimationOptions>(initialOptions);

  const onOptionsChange = useCallback((options: BaseAnimationOptions) => {
    const changed = JSON.stringify(options) !== JSON.stringify(initialOptions);
    setChanged(changed);
  }, []);

  return (
    <Dialog
      fullWidth
      maxWidth={"lg"}
      open={isOpen}
      onClose={onClose}
      PaperProps={{
        component: "form",
        onSubmit: (event: React.FormEvent<HTMLFormElement>) => {
          event.preventDefault();
          const formData = new FormData(event.currentTarget);
          const formJson = Object.fromEntries((formData as any).entries());
          const email = formJson.email;
          console.log(email);
          onClose();
        },
      }}
    >
      <DialogTitle>
        {animation?.animation?.name} ({animation?.animation?.id})
      </DialogTitle>
      <DialogContent>
        <Box
          sx={{
            height: 600,
          }}
        >
          <SimpleAnimationEditor
            options={optionsToEdit}
            onOptionsChange={onOptionsChange}
            optionsPrototype={animation?.optionsPrototype}
          />
          {/* {useSimpleEditor
              ? <SimpleAnimationEditor
                animation={animationToEdit?.animation}
                setChanged={setChanged}

                optionsPrototype={animationToEdit?.optionsPrototype}
              />
              : <AnimationEditor
                animation={animationToEdit?.animation}
                setAnimation={(animation) => {
                  console.log("saveAnimation", animation);
                }}
                optionsPrototype={animationToEdit?.optionsPrototype}
              />
            } */}
        </Box>
      </DialogContent>
      <DialogActions>
        <Button
          onClick={onClose}
        >
          Cancel
        </Button>
        <Button
          type="submit"
          disabled={!changed}
        // onClick=
        >Save
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default EditDialog;