import { Box, Checkbox, FormControlLabel, FormGroup, FormHelperText, TextField } from "@mui/material";
import { IMidiAnimationJSON } from "../../midi/animations/util";
import { OptionsPrototype } from "../../midi/animations/types";
import JSONPretty from "../JSONPretty";


const maybeStringify = (value: any) => {
  if (value === undefined) {
    return;
  }
  if (typeof value === "string") {
    return value;
  }
  if (typeof value === "object") {
    return JSON.stringify(value);
  }
  return String(value);
}

interface AnimationEditorProps {
  animation: IMidiAnimationJSON;
  setAnimation: (a: IMidiAnimationJSON) => void;
  optionsPrototype: OptionsPrototype;
}

const AnimationEditor = (props: AnimationEditorProps) => {
  const { animation, setAnimation, optionsPrototype = {} } = props;

  // Read all property names for this type and all parent types
  const fieldNames = Object.keys(optionsPrototype);
  return (<>
    <Box>
      <FormGroup>
        {fieldNames.map((fieldName, i) => {
          const value = animation.options[fieldName];
          let defaultValue: any;
          let type: string;
          const t = optionsPrototype[fieldName];
          switch (t) {
            case "string":
              defaultValue = "";
              type = "text";
              break;
            case "color": // TODO
              defaultValue = "";
              type = "text";
              break;
            case "number":
              defaultValue = 0;
              type = "number";
              break;
            case "boolean":
              defaultValue = false;
              type = "checkbox";
              break;
            default:
              defaultValue = value === undefined ? undefined : JSON.stringify(value);
              type = "text";
          }
          if (type === "checkbox") {
            return (<Box key={i}>
              <FormControlLabel
                sx={{ width: "100%" }}
                control={<Checkbox
                  checked={animation.options[fieldName]}
                  onChange={(e) => {
                    const newAnimation = { ...animation };
                    newAnimation.options[fieldName] = e.target.checked;
                    setAnimation(newAnimation);
                  }}
                />}
                label={fieldName} />
            </Box>)
          }
          return (<Box key={i}>
            <TextField
              sx={{ width: "100%" }}
              type={type}
              value={maybeStringify(animation.options[fieldName])}
              onChange={(e) => {
                const newAnimation = { ...animation };
                newAnimation.options[fieldName] = e.target.value;
                setAnimation(newAnimation);
              }}
            />
            <FormHelperText>{fieldName}</FormHelperText>
          </Box>);
        })}
      </FormGroup>
    </Box>
    <Box>
      <JSONPretty data={animation} />
    </Box>
  </>);
}


export default AnimationEditor;