import { uniqueTrackNames } from "./adapter";
import { Circles } from "./animations/Circles";
import { Cubes } from "./animations/Cubes";
import { Midi, IMidiAnimationRegistry } from "./types";

export const registerDemoAnimations = (source: Midi, registry: IMidiAnimationRegistry) => {
  const anims = createDemoAnimations(source);
  anims.forEach(anim => registry.addMidiAnimation(anim));
}

export const createDemoAnimations = (source: Midi) => {
  console.log("source", source);

  const trackNames = uniqueTrackNames(source);

  console.log("trackNames", trackNames);

  const startX = 200, endX = 1100;
  const startY = 200, endY = 600;
  // Create an array of origis evenly spaced across the canvas for each track names
  const origins = trackNames.map((trackName, i) => {
    const x = startX + (endX - startX) * i / trackNames.length;
    const y = startY + (endY - startY) * i / trackNames.length;
    return { x, y };
  });
  // Randomly sort the origins
  origins.sort(() => Math.random() - 0.5);

  return trackNames.map((trackName, i) => {
    const origin = origins[i % origins.length];
    return createDemoAnimation(i, trackName, origin);
  });
}

const rand = (max: number) => Math.round(Math.random() * max);

export const createDemoAnimation = (i: number, trackName: string, origin: { x: number, y: number }) => {
  const scale = 1.5 + rand(3.5)
  const lineWidth = 7 + rand(18);
  const diameter = 20 + rand(40);
  const randomColor = () => {
    const r = rand(255);
    const g = rand(255);
    const b = rand(255);
    return `rgba(${r},${g},${b},1)`;
  };
  const gradient = [0, 1, 2].map(() => randomColor());
  if (i % 2 === 0) {
    return new Cubes(
      { trackRef: trackName },
      {
        lineColor: { gradient, rotate: 90 },
        lineWidth,
        origin: { ...origin, x: 100 },
        top: true,
      });
  }
  return new Circles(
    { trackRef: trackName },
    {
      lineColor: { gradient, rotate: 90 },
      lineWidth,
      diameter,
      origin,
      scale,
    });
};