import { CombinedTrack, GroupedTrack, Track } from "../components/Editor/types";

const groupTracks = (tracks: Track[]): GroupedTrack[] => {
  const res = tracks.reduce((acc, track) => {
    const key = [track.name, track.instrument.family, track.instrument.name].join("-");
    if (!acc[key]) {
      acc[key] = { instrument: track.instrument, name: track.name, tracks: [] };
    }
    const combinedTrack = { ...track, tracks: [track] };
    acc[key].tracks.push(combinedTrack);
    return acc
  }, []);
  return Object.values(res) as GroupedTrack[];
}

const groupAndCombineGroupedTracks = (groupedTracks: GroupedTrack[]): GroupedTrack[] => {
  const tracksOverlap = (t: CombinedTrack, u: CombinedTrack) => {
    // Allow for a second of non-overlap
    return (t.minTimeSecs - 1 <= u.maxTimeSecs && t.maxTimeSecs >= u.minTimeSecs - 1);
  }
  // Create single CombinedTracks that each contain all the tracks that overlap.
  const combineGroupedTrack = (group: GroupedTrack) => {
    let changed = false;
    let times = 0;
    do {
      changed = false;
      // Try to merge any of the groups of tracks that overlap.
      for (let i = 0; i < group.tracks.length; i++) {
        const t = group.tracks[i];
        for (let j = i + 1; j < group.tracks.length; j++) {
          const u = group.tracks[j];
          if (tracksOverlap(t, u)) {
            const combinedTrack = {
              name: t.name,
              minTimeSecs: Math.min(t.minTimeSecs, u.minTimeSecs),
              maxTimeSecs: Math.max(t.maxTimeSecs, u.maxTimeSecs),
              duration: Math.max(t.duration, u.duration),
              instrument: t.instrument,
              tracks: [...t.tracks, ...u.tracks],
            };
            group.tracks[i] = combinedTrack;
            group.tracks.splice(j, 1);
            changed = true;
            break;
          }
        }
        if (changed) break;
      }
    } while (changed && ++times < 10);
  }
  groupedTracks.forEach(combineGroupedTrack);
  return groupedTracks;
}

export const groupAndCombineTracks = (tracks: Track[]) => {
  const groupedTracks = groupTracks(tracks);
  const groupedAndCombinedTracks = groupAndCombineGroupedTracks(groupedTracks);
  return groupedAndCombinedTracks;
}