import { Shapes } from "../util/Shapes";
import { IMidiAnimation, Note, NoteFilterCriteria } from "../types";
import { BaseAnimationOptions, ICirclesOptions } from "./types";
import { BaseMidiAnimation } from "./BaseMidiAnimation";

export interface Options extends BaseAnimationOptions, ICirclesOptions { }

/** 
 * These are the options for the Circles animation [[ICirclesOptions]]
 */
export class Circles extends BaseMidiAnimation<Options> implements IMidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, "circles", {
      diameter: "number",
      scale: "number",
    }, id);
  }

  public draw(notes: Note[], canvas: CanvasRenderingContext2D): void {
    const { height, width } = canvas.canvas;
    const shapes = new Shapes(canvas);

    const {
      diameter = 0,
      origin,
      scale = 1,
      fillColor = "rgba(0,0,0,0)",
      lineWidth = 5,
    } = this.options;

    const { x, y } = origin ?? { x: width / 2, y: height / 2 };

    const baseOptions = {
      ...this.options,
      fillColor,
      diameter,
      scale,
    }

    notes.forEach(note => {
      const val = note.midi * scale;
      const options = {
        ...baseOptions,
        lineWidth: lineWidth * note.velocity
      }
      shapes.circle(x, y, diameter + val, options);
    });
  }
}