import { useRef, useEffect, useMemo } from "react";
import useWebSocket, { ReadyState } from "react-use-websocket";
import { deserializeAnimation } from "../../midi/animations/util";
import "../../sass/index.scss";

const WS_URL = "ws://127.0.0.1:8000";

function isDrawEvent(message) {
  const parsedMessage = JSON.parse(message.data);
  return parsedMessage.type === "draw";
}

const randomName = () => {
  const sillyNames = [
    "Pickle Jar Hero",
    "The Great Potato",
    "Sexy Tofu",
    "Stained Undeeeez",
  ];
  const name = sillyNames[Math.floor(Math.random() * sillyNames.length)];
  const number = Math.floor(Math.random() * 1000);
  return `${name} ${number}`;
};

export function Listen() {
  const canvasElm = useRef<HTMLCanvasElement | null>(null);

  const username = randomName();

  const { sendJsonMessage, readyState } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log("WebSocket connection established.");
    },
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true
  });

  useEffect(() => {
    if (readyState === ReadyState.OPEN) {
      sendJsonMessage({ type: "userevent", username });
    }
  }, [readyState]);

  function Canvas() {
    const { lastJsonMessage } = useWebSocket(WS_URL, {
      share: true,
      filter: isDrawEvent,
    });

    const canvasContext = useMemo(() => {
      if (!canvasElm.current) return;
      canvasElm.current.style.width = "85%";
      canvasElm.current.style.height = "85%";
      canvasElm.current.width = canvasElm.current.offsetWidth;
      canvasElm.current.height = canvasElm.current.offsetHeight;
      const canvasContext = canvasElm.current.getContext("2d");
      return canvasContext;
    }, [canvasElm.current]);

    useEffect(() => {
      if (!lastJsonMessage) return;
      if (!canvasContext) return;

      // console.log("lastJsonMessage", lastJsonMessage);

      const { data: { drawPayloads } } = lastJsonMessage as any;

      canvasContext.clearRect(0, 0, canvasContext.canvas.width, canvasContext.canvas.height);
      for (const drawPayload of drawPayloads) {
        const { notes, animationJSON } = drawPayload;
        const animation = deserializeAnimation(animationJSON);
        animation.draw(notes, canvasContext);
      }
    }, [lastJsonMessage, canvasContext]);

    return <>
      <section className={"__display"}>
        <div className={"__canvas"}>
          <canvas ref={canvasElm}></canvas>
        </div>
      </section>
      <section className={"__username"}>
        {username}
      </section>
    </>;

  }

  return (<main className={"p-home"}>
    <Canvas />
  </main>)
}