import { useMemo } from "react";

import { Box, Grid, Stack, Typography } from "@mui/material";
import formatDuration from "format-duration";
import "../../sass/index.scss";
import { GroupedTrack } from "./types";



const Timeline = (props: {
  duration: number,
  selectedTrack: GroupedTrack | null,
}) => {

  const { duration, selectedTrack } = props;

  console.log("selectedTrack", selectedTrack);

  const tick = useMemo(() => 1000 * duration / 12, [duration]);

  const Header = () => {
    return (<>
      <Box sx={{
        width: "100%",
        backgroundColor: "grey.200",
        minHeight: 10,
        p: 1,
      }}>
        <Grid container>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(0 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(1 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(2 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(3 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(4 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(5 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(6 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(7 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(8 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(9 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(10 * tick)}</Typography>
          </Grid>
          <Grid item xs={1}>
            <Typography variant="subtitle2">| {formatDuration(11 * tick)}</Typography>
          </Grid>
        </Grid>
      </Box>
    </>);
  }

  const Notes = () => {
    return (<>
      <Box sx={{
        width: "100%",
        backgroundColor: "grey.50",
        minHeight: 100,
        p: 1,
      }}>
        {
          selectedTrack && selectedTrack.tracks.map((track, i) => {
            const left = 100 * track.minTimeSecs / duration;
            const width = 100 * (track.duration - track.minTimeSecs) / duration;
            return (
              <Stack
                sx={{
                  position: "relative",
                  top: 10,
                  left: `${left}%`,
                  width: `${width}%`,
                  height: 30,
                }}
                key={`track-${i}`}
              >
                <Box
                  sx={{
                    backgroundColor: "lightsalmon",
                    border: "1px solid salmon",
                    pl: 1,
                    pr: 1,
                    overflow: "hidden",
                    whiteSpace: "nowrap",
                    textOverflow: "ellipsis",
                  }}
                >
                  <Box sx={{ display: "flex", width: "100%", flexDirection: "row", alignItems: "center" }}>
                    <Box sx={{ flexGrow: 1, alignSelf: "center", alignContent: "center" }}>
                      <Typography variant="caption" component={"span"}>
                        <Typography variant="inherit" fontWeight={"bold"} component={"span"}>
                          #{track.tracks.map(t => t.index).sort().join(",")}
                        </Typography>
                        {" - "}
                        <Typography variant="inherit" fontStyle={"italic"} component={"span"}>
                          {formatDuration((track.duration - track.minTimeSecs) * 1000)}
                        </Typography>
                      </Typography>
                    </Box>
                  </Box>
                </Box>
              </Stack>
            );
          })
        }
      </Box>
    </>);
  }

  return (<>
    <Stack spacing={1}>
      <Header />
      <Notes />
    </Stack>
  </>);
}

export default Timeline;