import { Box } from "@mui/material";
import { IMidiAnimationJSON, toOptionsPrototypeDefaults } from "../../midi/animations/util";
import { JsonEditor as Editor } from 'jsoneditor-react';
import 'jsoneditor-react/es/editor.min.css';
import { BaseAnimationOptions, OptionsPrototype } from "../../midi/animations/types";
import { useState } from "react";

interface SimpleAnimationEditorProps {
  options: BaseAnimationOptions;
  onOptionsChange: (options: BaseAnimationOptions) => void;
  optionsPrototype: OptionsPrototype;
}

const SimpleAnimationEditor = (props: SimpleAnimationEditorProps) => {
  const { options, onOptionsChange, optionsPrototype = {} } = props;
  const defaults = toOptionsPrototypeDefaults(optionsPrototype);
  const initialValue = {
    ...defaults,
    ...options,
  };
  const [value, setValue] = useState(initialValue);
  return (<Box sx={{ height: "100%", width: "100%" }}>
    <Editor
      value={value}
      onChange={(value) => {
        const changed = JSON.stringify(value) !== JSON.stringify(initialValue);
        if (changed) {
          const valueWithoutDefaults = Object.keys(value).reduce((acc, key) => {
            if (value[key] !== defaults[key]) { // TODO: do deep compare
              acc[key] = value[key];
            }
            return acc;
          }) as BaseAnimationOptions;
          onOptionsChange(valueWithoutDefaults);
        }
      }}
      style={{ height: "100%", width: "100%" }}
    />
  </Box>);
}


export default SimpleAnimationEditor;