import RealJSONPretty from 'react-json-pretty';

const jsonTheme = {
  main: `line-height:1.3;color:#0f0f0f;background:white;overflow:auto;border-style:solid;
             border-color:#191923;border-width:1px;font-family:monospace,Lucida Console,Monaco`,
  key: `color:#703D57;`,
  string: `#7B0D1E`,
  value: `color:#2C5530;`,
  boolean: `color:#F39237;`
};

interface JSONPrettyProps {
  data: any;
}

const JSONPretty = (props: JSONPrettyProps) => {
  const { data } = props;

  return <RealJSONPretty theme={jsonTheme} data={data} />
}

export default JSONPretty;