const WS_URL = 'ws://127.0.0.1:8000';

function isUserEvent(message) {
  const parsedMessage = JSON.parse(message.data);
  return parsedMessage.type === 'userevent';
}

export function Serve() {
  const { audioElm } = useHome();

  return <>
    <main className={"p-home"}>
      {/* <section className={"__display"}> */}
      <div className="document-holder">
        <Canvas />
      </div>
      <div className="history-holder">
        <Users />
      </div>
      {/* </section> */}
      <audio controls
        src={`${process.env.PUBLIC_URL}/assets/nogo.mp3`}
        ref={audioElm}
        style={{
          width: '98%',
          margin: "auto",
        }}>
      </audio>
      {/* <ToastContainer /> */}
    </main>

  </>;
}

function Users() {
  const { lastJsonMessage } = useWebSocket(WS_URL, {
    share: true,
    filter: isUserEvent
  });


  let users = [];
  if (lastJsonMessage) {
    const msg = lastJsonMessage as any;
    const { data: { users: msgUsers, lastActivity } } = msg;
    users = Object.values(msgUsers || []);
  }

  console.log("lastJsonMessage", lastJsonMessage);
  console.log("users", users);

  return <>
    <ul className="history-list">
      {users.map((user: any) => (
        <li key={`user-${user.userId}`}>{user.username}</li>
      ))}
    </ul>
  </>;
}

