function useHome() {
  const audioElm = useRef<HTMLAudioElement | null>(null);
  const [wave, setWave] = useState<Wave | null>(null);

  const { sendJsonMessage, readyState } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log('WebSocket connection established.');
    },
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true
  });

  const callback = (data: any) => {
    if (!data) return;
    console.log("sendJsonMessage", data);
    sendJsonMessage({
      data,
      type: 'draw',
    });
  }

  useEffect(() => {
    if (!audioElm.current) return;
    if (!callback) return;
    if (wave) return;

    const init = async () => {
      const newWave = new Wave(audioElm.current, null);
      const registry = new MidiAnimationRegistry(callback, { verbose: false });
      const source = await newWave.addPlugin(registry, { eagerInit: true });

      registerDemoAnimations(source, registry);

      setWave(newWave);
    }
    init();
  }, [
    callback,
  ]);

  return {
    audioElm,
  }
}