import { v4 as uuidv4 } from "uuid";
import { NoteFilterCriteria } from "../types";
import { BaseAnimationOptions, OptionsPrototype } from "./types";

export class BaseMidiAnimation<O extends BaseAnimationOptions> {
  public readonly criteria: NoteFilterCriteria;
  public readonly options: O;
  public readonly id: string;
  public readonly name: string;
  public readonly optionsPrototype: OptionsPrototype;

  constructor(criteria: NoteFilterCriteria, options: O, name: string, optionsPrototype: OptionsPrototype, id?: string) {
    this.criteria = criteria;
    this.options = options;
    this.name = name;
    this.id = id || uuidv4();
    this.optionsPrototype = {
      origin: "point",
      fillColor: "fill",
      glow: "glow",
      lineColor: "fill",
      lineWidth: "number",
      ...optionsPrototype,
    };
  }
}
