import { GetNotesProps, Instrument, Note, Track, Midi } from './types';
import formatDuration from "format-duration";
import { Midi as ToneJSMidi, TrackJSON } from "@tonejs/midi";

interface Options {
  verbose?: boolean;
  debugJSON?: boolean;
  debugTracks?: boolean;
  debugNotes?: boolean;
  debugEachNotes?: boolean;
}

const createMidi = (input: ArrayBuffer, opts?: Options): Midi => {
  const midi = new ToneJSMidi(input);
  const midiJSON = midi.toJSON();

  const tracks = midiJSON.tracks.map((track, i) => {
    const newNotes = track.notes.map(note => {
      return {
        ...note,
        trackRef: i,
      };
    }) as Note[];
    const summary = {
      noteCount: newNotes.length,
      minTime: newNotes.reduce((acc, note) => Math.min(acc, note.time), Infinity),
      maxTime: newNotes.reduce((acc, note) => Math.max(acc, note.time), -Infinity),
      instrumentName: track.instrument.name,
      noteSet: Array.from(new Set(newNotes.map(note => note.midi))).sort(),
    }
    const _original = { ...track } as TrackJSON;
    delete _original.notes;
    return {
      ...track,
      index: i,
      notes: newNotes,
      instrumentRef: track.instrument.number,
      summary,
      _original,
    };
  }) as Track[];

  const instrumentJSONs = midi.tracks.map(track => track.instrument.toJSON());
  const instrumentMap = Object.fromEntries(instrumentJSONs.map((instrument, i) => [instrument.number, instrument]));
  const instruments = Object.values(instrumentMap) as Instrument[];

  const res = {
    tracks,
    instruments,
    _original: midiJSON,
  } as Midi;

  return res;
};

const getNotesFromSource = (source: Midi, props: GetNotesProps) => {
  const { startMillis, durationMillis, criteria } = props;

  if (criteria.trackRef === undefined) return;

  const { trackRef } = criteria;

  const selTracks = source.tracks.filter(track => {
    return track.index === trackRef || track.name === trackRef;
  });

  const notes = selTracks.flatMap(track => {
    return track.notes.filter(note => {
      const noteStartMillis = note.time * 1000;
      const noteEndMillis = noteStartMillis + note.duration * 1000;
      const endMillis = startMillis + durationMillis;
      if (noteEndMillis < startMillis) return false;
      if (noteStartMillis > endMillis) return false;
      return true;
    });
  });
  return notes;
}

export const createMidiNoteGetter = async (input: ArrayBuffer, opts?: Options) => {
  const { debugJSON, debugTracks = false, debugNotes, debugEachNotes } = opts || {};

  const source = createMidi(input, opts);

  if (debugJSON) {
    console.log("Printing JSON");
    console.log(JSON.stringify(source, null, 2));
  }
  if (debugTracks) {
    console.log("Printing tracks");
    source.tracks.forEach((track, i) => {
      console.log(` - ${i}: ${track.name} - ${JSON.stringify(track.summary)}`);
      if (debugNotes) {
        track.notes.forEach(note => {
          console.log("   - ", formatDuration(note.time * 1000), note);
        });
      }
    });
  }

  const getNotes = (props: GetNotesProps) => {
    const { startMillis, durationMillis, criteria } = props;
    const notes = getNotesFromSource(source, { startMillis, durationMillis, criteria });
    if (debugEachNotes) {
      const selTracks = Array.from(new Set(notes.map(n => n.trackRef)));
      console.log(
        "startMillis", startMillis,
        "durationMillis", durationMillis,
        "selTracks", selTracks,
        "notes", notes,
      );
    }
    return notes;
  }

  return { getNotes, source };
};