import React, { useMemo } from "react";

import { AccordionActions, Grid, IconButton, Stack, Typography } from "@mui/material";
import PlayIcon from "@mui/icons-material/PlayArrow";
import CancelIcon from "@mui/icons-material/Cancel";
import SaveIcon from "@mui/icons-material/Save";
import { styled } from "@mui/material/styles";
import ArrowForwardIosSharpIcon from "@mui/icons-material/ArrowForwardIosSharp";
import MuiAccordion, { AccordionProps } from "@mui/material/Accordion";
import MuiAccordionSummary, { AccordionSummaryProps, } from "@mui/material/AccordionSummary";
import MuiAccordionDetails from "@mui/material/AccordionDetails";
import { IMidiAnimationJSON } from "../../midi/animations/util";
import { GroupedTrack } from "./types";
import AnimationEditor from "./AnimationEditor";
import { OptionsPrototype } from "../../midi/animations/types";

export interface AnimationAndOptions {
  animation: IMidiAnimationJSON,
  optionsPrototype: OptionsPrototype,
}

// https://mui.com/material-ui/react-accordion/#customization
const Accordion = styled((props: AccordionProps) => (
  <MuiAccordion disableGutters elevation={0} square {...props} />
))(({ theme }) => ({
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&::before": {
    display: "none",
  },
}));

const AccordionSummary = styled((props: AccordionSummaryProps) => (
  <MuiAccordionSummary
    expandIcon={<ArrowForwardIosSharpIcon sx={{ fontSize: "0.9rem" }} />}
    {...props}
  />
))(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark"
      ? "rgba(255, 255, 255, .05)"
      : "rgba(0, 0, 0, .03)",
  flexDirection: "row-reverse",
  "& .MuiAccordionSummary-expandIconWrapper.Mui-expanded": {
    transform: "rotate(90deg)",
  },
  "& .MuiAccordionSummary-content": {
    marginLeft: theme.spacing(1),
  },
}));

const AccordionDetails = styled(MuiAccordionDetails)(({ theme }) => ({
  padding: theme.spacing(2),
  borderTop: "1px solid rgba(0, 0, 0, .125)",
}));

const showPlay = false;

const TrackPanel = (props: {
  i: number,
  track: GroupedTrack,
  expanded: boolean;
  handleChange: (panel: number) => (event: React.SyntheticEvent, newExpanded: boolean) => void,
  playTrack: () => void,
  save: () => void,
  cancel: () => void,
  animation: AnimationAndOptions | null,
  selected: boolean,
}) => {
  const { i, track, expanded, handleChange, playTrack, save, cancel, animation, selected } = props;

  const initialAnimation = animation?.animation;

  const [animationJSON, setAnimationJSON] = React.useState(initialAnimation);

  const dirty = useMemo(() => {
    return JSON.stringify(animationJSON) !== JSON.stringify(initialAnimation);
  }, [animationJSON]);

  const animationString = useMemo(() => {
    return animation?.animation?.name;
  }, [animation]);

  return <Accordion
    expanded={expanded}
    onChange={handleChange(i)}
    sx={{
      width: "100%",
    }}
  >
    <AccordionSummary
      expandIcon={undefined}
      sx={{
        backgroundColor: selected ? "rgba(0, 0, 0, .1)" : undefined,
      }}
    >
      <Grid container>
        <Grid item xs={5}>
          <Stack>
            <Typography variant="subtitle1">
              {track.name}{" "}{track.tracks.length}
            </Typography>
            <Typography variant="subtitle2">
              {track.instrument.name}
            </Typography>
          </Stack>
        </Grid>
        <Grid item xs={5}>
          <Typography variant="subtitle1" fontStyle="italic">
            {animationString}
          </Typography>
        </Grid>
        <Grid item xs={2}>
          {showPlay ?? <IconButton
            onClick={() => playTrack()}
          >
            <PlayIcon />
          </IconButton>
          }
        </Grid>
      </Grid>
    </AccordionSummary>
    <AccordionDetails>
      {animationJSON
        ? <AnimationEditor
          animation={animationJSON}
          setAnimation={setAnimationJSON}
          optionsPrototype={animation?.optionsPrototype}
        />
        : undefined
      }
    </AccordionDetails>
    <AccordionActions>
      <IconButton
        disabled={!dirty}
        onClick={cancel}
      >
        <CancelIcon />
      </IconButton>
      <IconButton
        disabled={!dirty}
        onClick={save}
      >
        <SaveIcon />
      </IconButton>
    </AccordionActions>
  </Accordion>
}

export default TrackPanel;