import { IPlugin, AudioSource, IPluginDrawProps } from '@foobar404/wave';
import { createMidiNoteGetter } from './impl';
import { IMidiAnimation, IMidiAnimationRegistry, Midi, MidiNoteGetter } from './types';

interface WavePluginProps {
  verbose?: boolean;
}

export class MidiAnimationRegistry implements IPlugin<Midi | undefined>, IMidiAnimationRegistry {
  private _midiNoteGetter: MidiNoteGetter | null = null;
  private _activeMidiAnimations: IMidiAnimation[] = [];
  private _verbose: boolean;

  public constructor(props?: WavePluginProps) {
    const { verbose } = props || {};
    this._verbose = verbose || false;
  }

  public get name(): string {
    return "MidiAnimationRegistry";
  }

  // TODO: we're calling this twice
  public async init(audioSource: AudioSource): Promise<Midi | undefined> {
    this._log("Loading midi file", audioSource, this._activeMidiAnimations);
    try {
      const audioUrl = audioSource.url;
      const ext = audioUrl.split(".").pop();
      const allButExt = audioUrl.slice(0, audioUrl.length - ext.length - 1);
      const midiUrl = `${allButExt}.mid`;
      const midiBuffer = midiUrl.startsWith("blob:")
        ? await (await (await fetch(midiUrl)).blob()).arrayBuffer()
        : await (await fetch(midiUrl)).arrayBuffer();
      const { getNotes, source } = await createMidiNoteGetter(midiBuffer, { verbose: this._verbose });
      this._midiNoteGetter = { getNotes };
      return source;
    } catch (e: any) {
      console.error("Error loading midi file", e);
    }
  }

  public async initFromMidi(midiUrl: string): Promise<Midi | undefined> {
    this._log("Loading midi file", midiUrl, this._activeMidiAnimations);
    try {
      const midiBuffer = midiUrl.startsWith("blob:")
        ? await (await (await fetch(midiUrl)).blob()).arrayBuffer()
        : await (await fetch(midiUrl)).arrayBuffer();
      const { getNotes, source } = await createMidiNoteGetter(midiBuffer, { verbose: this._verbose });
      this._midiNoteGetter = { getNotes };
      return source;
    } catch (e: any) {
      console.error("Error loading midi file", e);
    }
  }

  public draw(props: IPluginDrawProps): void {
    const { currentTimeMillis, durationMillis, canvas } = props;
    if (!this._midiNoteGetter) return;
    this._activeMidiAnimations.forEach((animation) => {
      const startMillis = currentTimeMillis
      const criteria = animation.criteria;
      const notes = this._midiNoteGetter.getNotes({
        startMillis,
        durationMillis,
        criteria,
      });
      animation.draw(notes, canvas);
    });
  }

  public addMidiAnimation(animation: IMidiAnimation): void {
    this._log("addMidiAnimation", animation);
    this._activeMidiAnimations.push(animation);
  }

  public clearMidiAnimations(): void {
    this._log("clearMidiAnimations");
    this._activeMidiAnimations = [];
  }

  private _log(...args: any[]): void {
    if (this._verbose) console.log("[MidiAnimationRegistry]", ...args);
  }
}